/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1a; /* Dark background color */
  color: #fff; /* Text color */
}

/* Container Styles */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #333; /* Dark container background */
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1);
}

/* Header Styles */
h1 {
  font-size: 2.5em;
  color: #03a9f4; /* Updated primary color */
  margin-bottom: 20px;
}

/* Roll Results Styles */
.roll-results table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.roll-results th,
.roll-results td {
  padding: 10px;
  text-align: center;
  border: 1px solid #fff;
}

.roll-results th {
  background-color: #222222;
  color: #4CAF50;
}

/* Button Container Styles */
.button-container {
  text-align: center;
  margin-top: 20px;
}

label {
  font-size: 1.2em;
  color: #03a9f4;
  margin-bottom: 10px;
}

input {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 10px;
  width: 100px;
}

button {
  padding: 15px 30px;
  font-size: 1.2em;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #4caf50; /* Updated success button color */
  color: #fff;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background-color: #388e3c; /* Darker green on hover */
}

/* Number Tally Styles */
.number-tally {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 20px;
}

.number-tally-item {
  text-align: center;
  margin: 10px;
}

.number-tally-item h3 {
  font-size: 24px;
  color: #03a9f4;
  margin-top: 0;
}

.number-tally-item p {
  margin: 0;
  font-size: 18px;
  color: #ffffff;
}

.number-tally-item h4 {
  font-size: 24px;
  color: #4CAF50;
}

.number-tally-item h5 {
  font-size: 24px;
  color: #FFD700;
}

/* Responsive Styles */
@media (max-width: 600px) {
  h1 {
    font-size: 2em;
  }

  button {
    padding: 12px 25px;
    font-size: 1em;
  }
}
